import styled from '@emotion/styled'
import { DEFAULT_SETTINGS } from '@src/lib/constants'
import { Db } from '@src/lib/db'
import { Button } from '@src/ui/components'
import Card from '@src/ui/components/Card'
import * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'
import * as Browser from 'webextension-polyfill'

const messages = defineMessages({
  header: {
    id: 'settings.general.resetAll.header',
    defaultMessage: 'Reset all settings',
  },
  cta: {
    id: 'settings.general.resetAll.cta',
    defaultMessage: 'This action is irreversible',
  },
  description: {
    id: 'settings.general.resetAll.description',
    defaultMessage:
      'All Toggl Track Browser Extension settings will be reset and you will be logged out',
  },
  button: {
    id: 'settings.general.resetAll.button',
    defaultMessage: 'Reset all settings to default',
  },
})

async function resetAllSettings() {
  await Browser.storage.sync.clear()
  Object.keys(DEFAULT_SETTINGS).forEach(
    (key: keyof typeof DEFAULT_SETTINGS) => {
      Db.set(key, DEFAULT_SETTINGS[key])
    }
  )
  window.location.reload()
}

const ResetAll: React.FC = () => {
  const handleButtonClick: React.MouseEventHandler<Element> =
    React.useCallback(() => {
      const isConfirmed = confirm(
        'Are you sure you want to reset your settings?'
      )
      if (isConfirmed) resetAllSettings()
    }, [])

  return (
    <Card header={<FormattedMessage {...messages.header} />}>
      <Content>
        <MessageWrapper>
          <FormattedMessage tagName={Cta} {...messages.cta} />
          <FormattedMessage tagName='span' {...messages.description} />
        </MessageWrapper>
        <StyledButton onClick={handleButtonClick}>
          <FormattedMessage {...messages.button} />
        </StyledButton>
      </Content>
    </Card>
  )
}

const Content = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 16px;
  padding-top: 16px;
`

const MessageWrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 4px;
`

const Cta = styled.h5`
  font-size: 14px;
  font-weight: ${({ theme }) => theme.typography.fontWeight.semibold};
`

const StyledButton = styled(Button)`
  height: 36px;
  width: 220px;
  color: ${({ theme }) => theme.colors.error_120.regular};
  font-size: 14px;
  font-weight: ${({ theme }) => theme.typography.fontWeight.semibold};
  white-space: nowrap;
`

export default ResetAll
