import styled from '@emotion/styled'
import { type Time, parseTime } from '@internationalized/date'
import { Db } from '@src/lib/db'
import useSetting from '@src/lib/hooks/useSetting'
import { DurationInput } from '@src/ui/components'
import Card from '@src/ui/components/Card'
import { TimeField } from '@src/ui/components/TimeField'
import * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'

import SwitchOption from '../../components/SwitchOption'

const messages = defineMessages({
  header: {
    id: 'settings.general.reminders.header',
    defaultMessage: 'Reminders and idle detection',
  },
  remindMeCta: {
    id: 'settings.general.reminders.remindMe.cta',
    defaultMessage: 'Remind me to track time',
  },
  remindMeDescription: {
    id: 'settings.general.reminders.remindMe.description',
    defaultMessage:
      "Receive a notification if you're not tracking time during specified hours",
  },
  remindMeConfig: {
    id: 'settings.general.reminders.remindMe.config',
    defaultMessage:
      'On weekdays from <fromTo>to</fromTo> remind me every {interval} minutes after the last time entry stopped.',
  },
  idleDetectionCta: {
    id: 'settings.general.reminders.idleDetection.cta',
    defaultMessage: 'Idle detection',
  },
  idleDetectionDescription: {
    id: 'settings.general.reminders.idleDetection.description',
    defaultMessage:
      "Receive a notification when you've been idle with a running timer. Allows you to discard the idle time.",
  },
  idleConfig: {
    id: 'settings.general.reminders.idleDetection.idleConfig',
    defaultMessage: 'Notify when idle for more than {secondsInput} seconds.',
  },
})

const Reminders: React.FC = () => {
  const [remindMe, setRemindMe] = useSetting('remindersCheckEnabled')
  const [idleDetection, setIdleDetection] = useSetting('idleDetectionEnabled')

  const handleRemindMeChange = (value: boolean) => {
    setRemindMe(value)
    Db.set('reminderManuallyEdited', true)
  }

  const handleIdleDetectionChange = (value: boolean) => {
    setIdleDetection(value)
  }

  return (
    <Card header={<FormattedMessage {...messages.header} />}>
      <Card.Section>
        <SwitchOption
          cta={messages.remindMeCta}
          description={messages.remindMeDescription}
          value={remindMe}
          onChange={handleRemindMeChange}
          dataHeapId='remind-tracking'
        />
        {remindMe && (
          <ConfigWrapper>
            <FormattedMessage
              {...messages.remindMeConfig}
              values={{
                fromTo: (chunks: React.ReactNode[]) => (
                  <FromToControl>{chunks}</FromToControl>
                ),
                interval: (
                  <IntervalControl
                    settingKey='remindersInterval'
                    minValue={0}
                  />
                ),
              }}
            />
          </ConfigWrapper>
        )}
      </Card.Section>
      <Card.Section>
        <SwitchOption
          cta={messages.idleDetectionCta}
          description={messages.idleDetectionDescription}
          value={idleDetection}
          onChange={handleIdleDetectionChange}
          dataHeapId='detect-idle-time'
        />
        {idleDetection && (
          <ConfigWrapper>
            <FormattedMessage
              {...messages.idleConfig}
              values={{
                secondsInput: (
                  <IntervalControl settingKey='idleInterval' minValue={15} />
                ),
              }}
            />
          </ConfigWrapper>
        )}
      </Card.Section>
    </Card>
  )
}

const FromToControl: React.FC<{
  children: React.ReactNode[] | React.ReactNode
}> = ({ children }) => {
  const [[from, to], setFromTo] = useSetting('remindersFromTo')
  const [hasError, setHasError] = React.useState(false)
  const [localFrom, setLocalFrom] = React.useState(parseTime(from))
  const [localTo, setLocalTo] = React.useState(parseTime(to))

  const handleFromChange = (value: Time) => {
    const isBeforeTo = value.compare(localTo) < 0
    setLocalFrom(value)

    if (!isBeforeTo) {
      return setHasError(true)
    }

    setFromTo([value.toString(), to])
    setHasError(false)
  }

  const handleToChange = (value: Time) => {
    const isAfterFrom = value.compare(localFrom) > 0
    setLocalTo(value)

    if (!isAfterFrom) {
      return setHasError(true)
    }

    setFromTo([from, value.toString()])
    setHasError(false)
  }

  return (
    <React.Fragment>
      <TimeField
        error={hasError}
        value={localFrom}
        onChange={handleFromChange}
      />
      {children}
      <TimeField error={hasError} value={localTo} onChange={handleToChange} />
    </React.Fragment>
  )
}

type Props = {
  settingKey: 'remindersInterval' | 'idleInterval'
  minValue: number
}

const IntervalControl: React.FC<Props> = ({ settingKey, minValue }) => {
  const [interval, setInterval] = useSetting(settingKey)
  const [inputValue, setInputValue] = React.useState(0)

  React.useEffect(() => {
    setInputValue(interval)
  }, [interval])

  const handleChange: React.ChangeEventHandler<HTMLInputElement> = (e) => {
    setInputValue(Number(e.target.value))
  }

  const handleBlur: React.FocusEventHandler<HTMLInputElement> = () => {
    const value = Math.max(minValue, inputValue)
    setInterval(value)
    setInputValue(value)
  }

  return (
    <StyledDuration
      type='number'
      value={inputValue}
      onChange={handleChange}
      onBlur={handleBlur}
    />
  )
}

const ConfigWrapper = styled.div`
  display: flex;
  align-items: center;
  padding-left: 53px;
`

const StyledDuration = styled(DurationInput)`
  display: inline-block;
  text-align: left;
  font-size: 14px;
  font-weight: ${({ theme }) => theme.typography.fontWeight.normal};
  width: 64px;
  margin: 0 4px 0 4px;

  border-radius: 8px;
  border: 1px solid ${({ theme }) => theme.colors.ui_040.regular};

  color: ${({ theme }) => theme.colors.ui.regular};
`

export default Reminders
