import { css } from '@emotion/react'
import styled from '@emotion/styled'
import Dropdown from '@src/components/Dropdown'
import useSetting from '@src/lib/hooks/useSetting'
import { Button, TextInput } from '@src/ui/components'
import Card from '@src/ui/components/Card'
import * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'
import Browser from 'webextension-polyfill'

import TogglOrigins from '@toggl/track-extension'

const messages = defineMessages({
  header: {
    id: 'settings.integrations.customURLs.header',
    defaultMessage: 'Custom URLs for integrations',
  },
  cta: {
    id: 'settings.integrations.customURLs.cta',
    defaultMessage:
      'If you use a tool hosted on a custom domain, you can enable it here. Enter the domain name in the format "toggl.com" and select the integration from the dropdown. Ports are not supported.',
  },
  button: {
    id: 'settings.integrations.customURLs.button',
    defaultMessage: 'Add',
  },
  delete: {
    id: 'settings.integrations.customURLs.delete',
    defaultMessage: 'Delete',
  },
})

const firstIntegration = Object.keys(TogglOrigins)[0]

const CustomURLs: React.FC = () => {
  const inputsContainer = React.useRef<HTMLDivElement>(null)
  const [integrations, setIntegrations] = useSetting('customIntegrations')
  const [domain, setDomain] = React.useState('')
  const [selectedIntegration, setSelectedIntegration] =
    React.useState(firstIntegration)

  const handleDomainChange = React.useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => setDomain(e.target.value),
    [setDomain]
  )

  const handleAddClick = React.useCallback(() => {
    if (domain.trim() === '') return

    const permission = { origins: [`*://*.${domain}/`] }

    void Browser.permissions.request(permission).then((response) => {
      if (!response) return

      setIntegrations({
        ...integrations,
        [domain]: TogglOrigins[selectedIntegration],
      })

      setDomain('')
      setSelectedIntegration(firstIntegration)
    })
  }, [domain, integrations, selectedIntegration, setIntegrations])

  const handleDeleteClick = React.useCallback(
    (key: string) => {
      const permission = { origins: [`*://*.${key}/`] }

      void Browser.permissions.remove(permission).then(() => {
        const { [key]: _, ...rest } = integrations
        setIntegrations(rest)
      })
    },
    [integrations, setIntegrations]
  )

  return (
    <Card header={<FormattedMessage {...messages.header} />}>
      <Card.Section>
        <FormattedMessage tagName='span' {...messages.cta} />
        <List>
          {Object.entries(integrations).map(([integration, { name }]) => (
            <ListItem key={integration}>
              <span>
                <CaptionItem>{integration}</CaptionItem>
                {` - ${name}`}
              </span>
              <DeleteButton onClick={() => handleDeleteClick(integration)}>
                <FormattedMessage {...messages.delete} />
              </DeleteButton>
            </ListItem>
          ))}
        </List>
        <Container ref={inputsContainer}>
          <StyledTextInput value={domain} onChange={handleDomainChange} />

          <Dropdown
            onOpenChange={(open) => {
              if (open) inputsContainer.current?.scrollIntoView()
            }}
            onItemClick={(id) => {
              setSelectedIntegration(id)
            }}
            css={css`
              width: 192px;
            `}
          >
            <Dropdown.Trigger height={36}>
              {TogglOrigins[selectedIntegration]?.name ||
                TogglOrigins[firstIntegration]?.name}
            </Dropdown.Trigger>
            <Dropdown.Overlay />
            <Dropdown.Content>
              {Object.entries(TogglOrigins).map(([integration, entry]) => (
                <Dropdown.Item key={integration} id={integration}>
                  {entry?.name}
                </Dropdown.Item>
              ))}
            </Dropdown.Content>
          </Dropdown>
        </Container>
        <StyledButton onClick={handleAddClick}>
          <FormattedMessage {...messages.button} />
        </StyledButton>
      </Card.Section>
    </Card>
  )
}

const List = styled.div`
  width: 400px;
  height: 100px;
  padding: 8px;
  gap: 4px;
  overflow: auto;

  background: ${({ theme }) => theme.colors.surface_primary.regular};
  border: 1px solid ${({ theme }) => theme.colors.ui_040.regular};
  border-radius: 8px;
`

const ListItem = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  line-height: 20px;
  padding: 0 20px 0 12px;
  font-size: 14px;
`

const CaptionItem = styled.span`
  font-weight: ${({ theme }) => theme.typography.fontWeight.bold};
`

const DeleteButton = styled.span`
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  font-size: 14px;
  font-weight: ${({ theme }) => theme.typography.fontWeight.semibold};
  color: ${({ theme }) => theme.colors.error_120.regular};
  cursor: pointer;
`

const Container = styled.div`
  display: flex;
  flex-direction: row;
  gap: 16px;
`

const StyledTextInput = styled(TextInput)`
  width: 192px;
  height: 36px;

  border-radius: 8px;
`

const StyledButton = styled(Button)`
  width: 74px;
`

export default CustomURLs
