import { css } from '@emotion/react'
import styled from '@emotion/styled'
import type * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'

import heroSrc from './changelogHero.svg'

const messages = defineMessages({
  title: {
    id: 'settings.whatsNew.banner.title',
    defaultMessage: `Discover What's Unfolded!`,
  },
  description: {
    id: 'settings.whatsNew.banner.description',
    defaultMessage: `Get an overview of the extension's most recent updates. From new features to bug {br} fixes, Stay in the loop!`,
  },
})

export const Banner: React.FC = () => {
  return (
    <Root>
      <div
        css={css`
          padding-top: 26px;
        `}
      >
        <Title>
          <FormattedMessage {...messages.title} />
        </Title>
        <DescriptionsContainer>
          <Description>
            <FormattedMessage
              {...messages.description}
              values={{ br: <br /> }}
            />
          </Description>
        </DescriptionsContainer>
      </div>
      <div>
        <img src={heroSrc} alt='' />
      </div>
    </Root>
  )
}

const Root = styled.div`
  display: flex;
  justify-content: space-between;
  position: relative;
  color: ${({ theme }) => theme.colors.ui.regular};
  background-color: ${({ theme }) =>
    theme.name.includes('dark')
      ? theme.colors.surface_primary.regular
      : theme.colors.pale_peach.regular};
  padding: 0 128px 0 32px;

  & > div {
    gap: 16px;
    display: flex;
    flex-direction: column;
  }
`

const Title = styled.h2`
  font-weight: 700;
  font-size: 24px;
  line-height: 167%;
`

const DescriptionsContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding-bottom: 26px;
`

const Description = styled.span`
  font-weight: 400;
  font-size: 14px;
  line-height: 143%;
`
