import styled from '@emotion/styled'
import { Button, Header } from '@src/ui/components'
import { TEListService, useTEListStorage } from '@src/v4/services/TEListService'
import { FormattedMessage, defineMessages } from 'react-intl'

const messages = defineMessages({
  header: {
    id: 'settings.debug.te.header',
    defaultMessage: 'Debug Time Entries',
  },
  fetchList: {
    id: 'settings.debug.te.fetchList',
    defaultMessage: 'Fetch TEs List Into Storage',
  },
  clearList: {
    id: 'settings.debug.te.clearList',
    defaultMessage: 'Clear TEs List Storage',
  },
  storage: {
    id: 'settings.debug.te.storage',
    defaultMessage: 'Time Entries Storage (Sliced to Last 10 Entries)',
  },
})

export const TimeEntriesList = () => {
  const [storageTEList] = useTEListStorage()

  const handleListFetch = async () => {
    await TEListService.invoke({ store: true })
  }

  const handleListClear = async () => {
    await TEListService.clear()
  }

  const slicedTEs = storageTEList
    ? Object.fromEntries(Object.entries(storageTEList).slice(0, 10))
    : null

  return (
    <Root>
      <FormattedMessage tagName={Header} {...messages.header} />
      <Content>
        <Flex css={{ gap: 16 }}>
          <Button onClick={handleListFetch}>
            <FormattedMessage {...messages.fetchList} />
          </Button>
          <Button onClick={handleListClear}>
            <FormattedMessage {...messages.clearList} />
          </Button>
        </Flex>

        <fieldset style={{ padding: 16, fontSize: 14 }}>
          <legend>
            <FormattedMessage {...messages.storage} />
          </legend>
          <pre>{JSON.stringify(slicedTEs, null, 2)}</pre>
        </fieldset>
      </Content>
    </Root>
  )
}

const Flex = styled.div`
  display: flex;
`

const Root = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  color: white;
`

const Content = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 24px;
  gap: 16px;
`
