import styled from '@emotion/styled'
import { AppThemeProvider } from '@src/components/AppThemeProvider'
import { abTester } from '@src/lib/abTester'
import { useReminderNotificationExperiment } from '@src/lib/hooks/useReminderNotificationExperiment'
import { ScrollToId } from '@src/pages/settings/useScrollToId'
import ArrowIcon from '@src/ui/icons/ArrowIcon'
import PomodoroIcon from '@src/ui/icons/PomodoroIcon'
import PrivateIcon from '@src/ui/icons/PrivateIcon'
import QuestionIcon from '@src/ui/icons/QuestionIcon'
import TelemetryIcon from '@src/ui/icons/TelemetryIcon'
import { WhatsNewIcon } from '@src/ui/icons/WhatsNewIcon'
import { useUserStorage } from '@src/v4/services/UsersService'
import Logo from '@ui/icons/Logo'
import SettingsIcon from '@ui/icons/SettingsIcon'
import * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'
import { HashRouter, NavLink, Navigate, Route, Routes } from 'react-router-dom'

import styles from '@toggl/style'

import { TimeEntriesList } from './tabs/Debug/TimeEntriesList'
import GeneralPage from './tabs/General'
import IntegrationsPage from './tabs/Integrations'
import MyAccountPage from './tabs/MyAccount'
import PomodoroPage from './tabs/Pomodoro'
import TelemetryPage from './tabs/Telemetry'
import { WhatsNew } from './tabs/WhatsNew'

const messages = defineMessages({
  settingsDivider: {
    id: 'settings.nav.settingsDivider',
    defaultMessage: 'Settings',
  },
  general: {
    id: 'settings.nav.general',
    defaultMessage: 'General',
  },
  integrations: {
    id: 'settings.nav.integrations',
    defaultMessage: 'Integrations',
  },
  pomodoro: {
    id: 'settings.nav.pomodoro',
    defaultMessage: 'Pomodoro',
  },
  telemetry: {
    id: 'settings.nav.telemetry',
    defaultMessage: 'Telemetry',
  },
  myAccount: {
    id: 'settings.nav.myAccount',
    defaultMessage: 'My Account',
  },
  helpDivider: {
    id: 'settings.nav.helpDivider',
    defaultMessage: 'Help',
  },
  whatsNew: {
    id: 'settings.nav.whatsNew',
    defaultMessage: 'Whats New',
  },
  userGuide: {
    id: 'settings.nav.userGuide',
    defaultMessage: 'User Guide',
  },
  debug: {
    id: 'settings.nav.debug',
    defaultMessage: 'Debug',
  },
  teStorage: {
    id: 'settings.nav.teStorage',
    defaultMessage: 'Time Entries',
  },
})

const Settings = () => {
  const [user] = useUserStorage()
  const [, forceUpdate] = React.useReducer((x) => x + 1, 0)

  useReminderNotificationExperiment()

  React.useEffect(() => {
    if (user) {
      abTester.setAttributes({ id: user.id })
      abTester.subscribe(() => {
        forceUpdate()
      })
      if (window.heap) window.heap.identify(`${user.id}`)
    }
  }, [user])

  const __DEV__ = process.env.NODE_ENV === 'development'

  return (
    <HashRouter>
      <Root>
        <AppThemeProvider enforceColorScheme='light'>
          <Sidebar>
            <Header>
              <Logo width='118px' height='24px' />
            </Header>
            <NavContent>
              <Group>
                <Divider>
                  <FormattedMessage {...messages.settingsDivider} />
                </Divider>
                <NavButton to='/integrations'>
                  <ArrowIcon next />
                  <FormattedMessage {...messages.integrations} />
                </NavButton>
                <NavButton to='/general'>
                  <SettingsIcon />
                  <FormattedMessage {...messages.general} />
                </NavButton>
                <NavButton to='/pomodoro'>
                  <PomodoroIcon />
                  <FormattedMessage {...messages.pomodoro} />
                </NavButton>
                <NavButton to='/telemetry'>
                  <TelemetryIcon />
                  <FormattedMessage {...messages.telemetry} />
                </NavButton>
                <NavButton to='/account'>
                  <PrivateIcon />
                  <FormattedMessage {...messages.myAccount} />
                </NavButton>
              </Group>
              <Group>
                <Divider>
                  <FormattedMessage {...messages.helpDivider} />
                </Divider>
                <NavButton to='/whats-new'>
                  <WhatsNewIcon />
                  <FormattedMessage {...messages.whatsNew} />
                </NavButton>
                <NavButton
                  to='https://support.toggl.com/en/articles/2206984-toggl-track-browser-extension'
                  target='_blank'
                  rel='noreferrer noopener'
                >
                  <QuestionIcon />
                  <FormattedMessage {...messages.userGuide} />
                </NavButton>
              </Group>
              {__DEV__ && (
                <Group>
                  <Divider>
                    <FormattedMessage {...messages.debug} />
                  </Divider>
                  <NavButton to='/time-entries'>
                    <WhatsNewIcon />
                    <FormattedMessage {...messages.teStorage} />
                  </NavButton>
                </Group>
              )}
            </NavContent>
          </Sidebar>
        </AppThemeProvider>
        <Content>
          <Routes>
            <Route path='/general/*' element={<GeneralPage />} />
            <Route path='/integrations/*' element={<IntegrationsPage />} />
            <Route path='/pomodoro/*' element={<PomodoroPage />} />
            <Route path='/telemetry/*' element={<TelemetryPage />} />
            <Route path='/account/*' element={<MyAccountPage />} />
            <Route path='/whats-new/*' element={<WhatsNew />} />
            <Route path='/time-entries' element={<TimeEntriesList />} />
            <Route path='/' element={<Navigate to='/integrations' />} />
          </Routes>
        </Content>
      </Root>
      <ScrollToId />
    </HashRouter>
  )
}

const Root = styled.div`
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: row;
`

const Header = styled.div`
  height: 70px;
  width: 100%;
`

const Content = styled.div`
  overflow: auto;
  width: 100%;
  height: 100%;
  background-color: ${({ theme }) => theme.colors.surface_secondary.regular};
`

const Sidebar = styled.div`
  width: 180px;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 16px;
  background-color: #2c1338;
`

const NavContent = styled.div`
  display: flex;
  flex-direction: column;
  gap: 30px;
`

const Group = styled.div`
  display: flex;
  flex-direction: column;
  gap: 6px;
`

const Divider = styled.div`
  color: ${({ theme }) => theme.colors.ui_060.regular};
  font-size: 11px;
  line-height: normal;
  font-weight: ${({ theme }) => theme.typography.fontWeight.semibold};
  text-transform: uppercase;
  letter-spacing: 0.4px;
`

const NavButton = styled(NavLink)`
  position: relative;
  height: 32px;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 6px 8px;
  gap: 12px;
  border-radius: 8px;
  font-size: 14px;
  color: ${styles.colors.pale_peach.regular};
  &.active {
    color: ${styles.colors.pale_peach.onClick};
    background-color: ${styles.colors.ui_090.regular};
  }
  cursor: pointer;
  text-decoration: none;

  &:hover {
    color: ${styles.colors.pale_peach.hover};
    background-color: ${styles.colors.ui_090.regular};
  }
`

export default Settings
