// eslint-disable-next-line @toggl/no-directory-named-imports
import { css, useTheme } from '@emotion/react'
import styled from '@emotion/styled'
import { Db } from '@src/lib/db'
import useSetting from '@src/lib/hooks/useSetting'
import { track } from '@src/lib/utils'
import { Button, TextInput } from '@src/ui/components'
import Card from '@src/ui/components/Card'
import { SearchIcon } from '@src/ui/icons/SearchIcon'
import * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'
import Browser from 'webextension-polyfill'

import TogglOrigins from '@toggl/track-extension'
import { Checkbox } from '@toggl/ui'

const messages = defineMessages({
  integrationsListHeader: {
    id: 'settings.integrations.listHeader',
    defaultMessage: 'Integrations',
  },
  integrationsListInfo: {
    id: 'settings.integrations.integrationsListInfo',
    defaultMessage:
      'Enable the tools you want to use with Toggl Track Browser Extension. You can enable everything by clicking the "Enable all" button.',
  },
  enableAllButton: {
    id: 'settings.integrations.enableAllButton',
    defaultMessage: 'Enable All',
  },
  disableAllButton: {
    id: 'settings.integrations.disableAllButton',
    defaultMessage: 'Disable All',
  },
})

export default function IntegrationsPage() {
  const theme = useTheme()
  const [filter, setFilter] = React.useState('')
  const filteredIntegrations = React.useMemo(
    () =>
      Object.entries(TogglOrigins).filter(([key, entry]) =>
        `${entry.name} - ${key}`.toLowerCase().includes(filter)
      ),
    [filter]
  )

  const [selectedIntegrations, setSelectedIntegrations] = useSetting(
    'integrations',
    true
  )

  const onSelectIntegration = (key: string) => {
    const permission = { origins: [`*://*.${key}/`] }

    if (selectedIntegrations[key]) {
      void Browser.permissions.remove(permission).then(() =>
        setSelectedIntegrations({
          ...selectedIntegrations,
          [key]: !selectedIntegrations[key],
        })
      )
    } else {
      void Browser.permissions.request(permission).then((response) => {
        if (!response) return

        setSelectedIntegrations({
          ...selectedIntegrations,
          [key]: !selectedIntegrations[key],
        })
      })
    }
  }

  const onApplyAll = (status: boolean) => {
    if (status) {
      void Browser.permissions.getAll().then((permissions) => {
        if (permissions.origins?.includes('<all_urls>'))
          return Db.set(
            'integrations',
            Object.keys(TogglOrigins).reduce((acc, key) => {
              acc[key] = true
              return acc
            }, {})
          )
      })

      void Browser.permissions.request({ origins: ['<all_urls>'] })
      track('enable-integrations', { context: 'Enable all web' })
    } else {
      void Browser.permissions.remove({ origins: ['<all_urls>'] }).then(() =>
        setSelectedIntegrations(
          Object.keys(TogglOrigins).reduce((acc, key) => {
            acc[key] = status
            return acc
          }, {})
        )
      )
    }
  }

  return (
    <Card header={<FormattedMessage {...messages.integrationsListHeader} />}>
      <Card.Section>
        <FormattedMessage {...messages.integrationsListInfo} />

        <ListContainer>
          <List>
            <div
              css={css`
                position: relative;
                display: flex;
                align-items: center;
                margin-bottom: 4px;

                & > svg {
                  position: absolute;
                  left: 8px;
                }
              `}
            >
              <SearchIcon />
              <TextInput
                css={css`
                  border: 1px solid ${theme.colors.ui_040.regular};
                  height: 30px;
                  padding-left: 30px;
                `}
                value={filter}
                placeholder='Filter integrations'
                onChange={(e) => setFilter(e.target.value)}
              />
            </div>

            {filteredIntegrations.map(
              ([key, entry]: [string, { name: string }]) => (
                <ListItem key={key} htmlFor={`integration-${key}`}>
                  <Checkbox
                    id={`integration-${key}`}
                    checked={selectedIntegrations[key]}
                    onClick={() => onSelectIntegration(key)}
                  />
                  <span
                    css={css`
                      white-space: nowrap;
                      text-overflow: ellipsis;
                      overflow: hidden;
                    `}
                  >{`${entry.name} - ${key}`}</span>
                </ListItem>
              )
            )}
          </List>
        </ListContainer>

        <ButtonWrapper>
          <Button onClick={() => onApplyAll(true)}>
            <FormattedMessage {...messages.enableAllButton} />
          </Button>
          <Button
            onClick={() => onApplyAll(false)}
            dataHeapId='extension:disable-integrations'
          >
            <FormattedMessage {...messages.disableAllButton} />
          </Button>
        </ButtonWrapper>
      </Card.Section>
    </Card>
  )
}

const ListContainer = styled.div`
  border-radius: 14px;
  width: 400px;
  overflow: hidden;
  border: 1px solid ${({ theme }) => theme.colors.ui_040.regular};
`

const List = styled.div`
  width: 400px;
  height: 334px;
  padding: 8px;
  gap: 4px;
  overflow: auto;

  background: ${({ theme }) => theme.colors.surface_primary.regular};
`

const ListItem = styled.label`
  display: flex;
  align-items: center;
  gap: 8px;
  line-height: 28px;
  cursor: pointer;
  padding: 0 8px;
  border-radius: 4px;

  &:hover {
    background-color: ${({ theme }) => theme.colors.surface_secondary.hover};
  }
`

const ButtonWrapper = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 8px;
  width: calc(113px * 2 + 8px);
`
