import styled from '@emotion/styled'
import { Button } from '@src/ui/components'
import Card from '@src/ui/components/Card'
import type * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'
import * as Browser from 'webextension-polyfill'

const messages = defineMessages({
  header: {
    id: 'settings.general.clearCache.header',
    defaultMessage: 'Clear cache',
  },
  description: {
    id: 'settings.general.clearCache.description',
    defaultMessage: `Clear all the extension's cached data.`,
  },
  button: {
    id: 'settings.general.clearCache.button',
    defaultMessage: 'Clear cache',
  },
})

async function clearCache() {
  await Browser.storage.local.clear()
}

const ClearCache: React.FC = () => {
  return (
    <Card header={<FormattedMessage {...messages.header} />}>
      <Content>
        <MessageWrapper>
          <FormattedMessage tagName='span' {...messages.description} />
        </MessageWrapper>
        <StyledButton onClick={() => clearCache()}>
          <FormattedMessage {...messages.button} />
        </StyledButton>
      </Content>
    </Card>
  )
}

const Content = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 16px;
  padding-top: 16px;
`

const MessageWrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 4px;
`

const StyledButton = styled(Button)`
  height: 36px;
  width: 220px;
  color: ${({ theme }) => theme.colors.error_120.regular};
  font-size: 14px;
  font-weight: ${({ theme }) => theme.typography.fontWeight.semibold};
  white-space: nowrap;
`

export default ClearCache
