import { useTheme } from '@emotion/react'
import styled from '@emotion/styled'
import { CrossIcon } from '@src/ui/icons/CrossIcon'
import type * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'

import heroSrc from './hero.svg'

const messages = defineMessages({
  title: {
    id: 'settings.onboardingBanner.title',
    defaultMessage: 'Welcome!',
  },
  description: {
    id: 'settings.onboardingBanner.description',
    defaultMessage: `This is the settings page for Track extension. {br}
    Come here whenever you need to tailor your extension for the most comfortable use.`,
  },
  greetings: {
    id: 'settings.onboardingBanner.greetings',
    defaultMessage: 'Happy tracking!',
  },
})

export const OnboardingBanner: React.FC<{
  onClose: React.MouseEventHandler
}> = (props) => {
  const theme = useTheme()

  return (
    <Root>
      <CloseIconContainer onClick={props.onClose}>
        <CrossIcon color={theme.colors.ui_050.regular} />
      </CloseIconContainer>
      <div>
        <Title>
          <FormattedMessage {...messages.title} />
        </Title>
        <DescriptionsContainer>
          <Description>
            <FormattedMessage
              {...messages.description}
              values={{ br: <br /> }}
            />
          </Description>
          <Description>
            <strong>
              <FormattedMessage {...messages.greetings} />
            </strong>
          </Description>
        </DescriptionsContainer>
      </div>
      <div>
        <img src={heroSrc} alt='' />
      </div>
    </Root>
  )
}

const Root = styled.div`
  display: flex;
  justify-content: space-between;
  position: relative;
  color: ${({ theme }) => theme.colors.ui.regular};
  background-color: ${({ theme }) =>
    theme.name.includes('dark')
      ? theme.colors.surface_primary.regular
      : theme.colors.pale_peach.regular};
  padding: 26px 128px 0 32px;

  & > div {
    gap: 16px;
    display: flex;
    flex-direction: column;
  }
`

const Title = styled.h2`
  font-weight: 700;
  font-size: 24px;
  line-height: 167%;
`

const DescriptionsContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding-bottom: 26px;
`

const Description = styled.span`
  font-weight: 400;
  font-size: 14px;
  line-height: 143%;
`

const CloseIconContainer = styled.div`
  position: absolute;
  right: 20px;
  top: 20px;
  cursor: pointer;
`
