// TODO: I should not be in this package, but on `NotificationState`. Please move me there when #10515 is resolved.
type ServerNotification = {
  actions?:
    | {
        default: string
      }
    | {
        [key: string]: {
          type: string
          code: string
        }
      }
  class: number
  content: {
    default_message?: string
    defaultMessage?: string // TODO: obsolete, remove it
    id: string
    values?:
      | Record<string, React.ReactNode | ((chunks: string) => React.ReactNode)>
      | null
      | undefined
  }
  created_at: string
  id: number
  notification_type: string
  read_at?: string | null
  title: {
    default_message?: string
    defaultMessage?: string // TODO: obsolete, remove it
    id: string
    values?:
      | Record<string, React.ReactNode | ((chunks: string) => React.ReactNode)>
      | null
      | undefined
  }
  user_id: number
}

type LocalNotification = Pick<
  ServerNotification,
  'actions' | 'title' | 'content'
> & {
  id: string
  created_at: number
  localOnly: true
}

export type Notification = ServerNotification | LocalNotification

