import styled from '@emotion/styled'
import useSetting from '@src/lib/hooks/useSetting'
import { parseDuration } from '@src/lib/timerUtils'
import { track } from '@src/lib/utils'
import { DurationInput } from '@src/ui/components'
import Card from '@src/ui/components/Card'
import * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'

import SwitchOption from '../../components/SwitchOption'

const messages = defineMessages({
  header: {
    id: 'settings.general.startingStopping.header',
    defaultMessage: 'Starting and stopping the timer',
  },
  startAutomaticallyCta: {
    id: 'settings.general.startingStopping.startAutomatically.cta',
    defaultMessage: 'Start timer automatically',
  },
  startAutomaticallyDescription: {
    id: 'settings.general.startingStopping.startAutomatically.description',
    defaultMessage: `Start a timer with your most recent time entry's details when the browser is opened`,
  },
  stopAutomaticallyCta: {
    id: 'settings.general.startingStopping.stopAutomatically.cta',
    defaultMessage: 'Stop timer automatically',
  },
  stopAutomaticallyDescription: {
    id: 'settings.general.startingStopping.stopAutomatically.description',
    defaultMessage: 'Stop the timer when the browser is closed',
  },
  stopAtDayEndCta: {
    id: 'settings.general.startingStopping.stopAtDayEnd.cta',
    defaultMessage: 'Stop timer automatically at a specified time of day',
  },
  stopAtDayEndDescription: {
    id: 'settings.general.startingStopping.stopAtDayEnd.description',
    defaultMessage:
      'Stop the timer automatically when it reaches your specified time',
  },
  stopAtDayEndLabel: {
    id: 'settings.general.startingStopping.stopAtDayEnd.label',
    defaultMessage: 'Stop time:',
  },
})

const StartingStopping: React.FC = () => {
  const [startAutomatically, setStartAutomatically] =
    useSetting('startAutomatically')
  const [stopAutomatically, setStopAutomatically] =
    useSetting('stopAutomatically')
  const [stopAtDayEnd, setStopAtDayEnd] = useSetting('stopAtDayEnd')
  const [dayEndTime, setDayEndTime] = useSetting('dayEndTime')
  const [inputValue, setInputValue] = React.useState(dayEndTime)

  React.useEffect(() => {
    setInputValue(dayEndTime)
  }, [dayEndTime])

  const handleStartAutomaticallyChange = React.useCallback(
    (value) => {
      setStartAutomatically(value)
      track('automate-timer', { context: 'Start', value })
    },
    [setStartAutomatically]
  )

  const handleStopAutomaticallyChange = React.useCallback(
    (value) => {
      setStopAutomatically(value)
      track('automate-timer', { context: 'Stop', value })
    },
    [setStopAutomatically]
  )

  const handleStopAtDayEndChange = React.useCallback(
    (value) => {
      setStopAtDayEnd(value)
      track('automate-timer', { context: 'Stop At Set Time', value })
    },
    [setStopAtDayEnd]
  )

  const handleInputChange = React.useCallback(
    (e?: React.ChangeEvent<HTMLInputElement>) => {
      setInputValue(e?.target.value || '')
    },
    []
  )

  const handleDayEndTimeBlur = React.useCallback(() => {
    const durationString = parseDuration(inputValue).toISOTime({
      suppressSeconds: true,
    })
    setDayEndTime(durationString ?? '')
    setInputValue(durationString ?? '')
  }, [inputValue, setDayEndTime])

  return (
    <Card header={<FormattedMessage {...messages.header} />}>
      <Card.Section>
        <SwitchOption
          cta={messages.startAutomaticallyCta}
          description={messages.startAutomaticallyDescription}
          value={startAutomatically}
          onChange={handleStartAutomaticallyChange}
        />
      </Card.Section>
      <Card.Section>
        <SwitchOption
          cta={messages.stopAutomaticallyCta}
          description={messages.stopAutomaticallyDescription}
          value={stopAutomatically}
          onChange={handleStopAutomaticallyChange}
        />
      </Card.Section>
      <Card.Section>
        <SwitchOption
          cta={messages.stopAtDayEndCta}
          description={messages.stopAtDayEndDescription}
          value={stopAtDayEnd}
          onChange={handleStopAtDayEndChange}
        />
        {stopAtDayEnd && (
          <DurationContainer>
            <TimeLabel>
              <FormattedMessage {...messages.stopAtDayEndLabel} />
            </TimeLabel>
            <StyledDuration
              value={inputValue}
              onChange={handleInputChange}
              onBlur={handleDayEndTimeBlur}
            />
          </DurationContainer>
        )}
      </Card.Section>
    </Card>
  )
}

const DurationContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 8px;
  margin-left: 54px;
  width: 300px;
  gap: 4px;
`

const TimeLabel = styled.span`
  text-transform: uppercase;
  font-size: 11px;
  font-weight: ${({ theme }) => theme.typography.fontWeight.semibold};
  color: ${({ theme }) => theme.colors.ui_060.regular};
`

const StyledDuration = styled(DurationInput)`
  text-align: left;
  font-size: 14px;
  font-weight: ${({ theme }) => theme.typography.fontWeight.normal};

  border-radius: 8px;
  border: 1px solid ${({ theme }) => theme.colors.ui_040.regular};

  color: ${({ theme }) => theme.colors.ui.regular};
`

export default StartingStopping
