import { useTheme } from '@emotion/react'
import styled from '@emotion/styled'
import useSetting from '@src/lib/hooks/useSetting'
import { Badge } from '@src/packages/ui/Badge'
import { useAppTheme } from '@src/shared/utils/useAppTheme'
import Card from '@src/ui/components/Card'
import * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'

import SwitchOption from '../../components/SwitchOption'

const messages = defineMessages({
  header: {
    id: 'settings.general.other.header',
    defaultMessage: 'Other settings',
  },
  tinyButtonCta: {
    id: 'settings.general.other.tinyButton.cta',
    defaultMessage: 'Enable Tiny Button',
  },
  tinyButtonDescription: {
    id: 'settings.general.other.tinyButton.description',
    defaultMessage: `Adds a button in all of your browser's tabs, so that you can track time from everywhere.`,
  },
  taggingCta: {
    id: 'settings.general.other.tagging.cta',
    defaultMessage: 'Enable automatic tagging',
  },
  taggingDescription: {
    id: 'settings.general.other.tagging.description',
    defaultMessage:
      'If an integration supports tags or labels, automatically add them to the time entry. Not available in all integrations.',
  },
  postPopupCta: {
    id: 'settings.general.other.postPopup.cta',
    defaultMessage: 'Open edit popup after starting an integration timer',
  },
  postPopupDescription: {
    id: 'settings.general.other.postPopup.description',
    defaultMessage:
      'Automatically opens the edit form immediately after clicking a "Start timer" link in any integration',
  },
  rightClickCta: {
    id: 'settings.general.other.rightClick.cta',
    defaultMessage: 'Show "Start timer" option in browser right click menu',
  },
  rightClickDescription: {
    id: 'settings.general.other.rightClick.description',
    defaultMessage:
      'Select text and start a timer with that description from the right-click menu. If nothing is selected, the page title is used as the description.',
  },
  darkModeCta: {
    id: 'settings.general.other.darkMode.cta',
    defaultMessage: 'Enable dark mode',
  },
  darkModeDescription: {
    id: 'settings.general.other.darkMode.description',
    defaultMessage: 'Use dark mode for Toggl Track Browser Extension',
  },
  new: {
    id: 'settings.general.other.new',
    defaultMessage: 'New',
  },
})

const Other: React.FC = () => {
  const theme = useTheme()
  const [enableTinyButton, setEnableTinyButton] = useSetting('enableTinyButton')
  const [tagging, setTagging] = useSetting('enableAutoTagging')
  const [postPopup, setPostPopup] = useSetting('showPostPopup')
  const [rightClick, setRightClick] = useSetting('showRightClickButton')
  const { appTheme, enableDarkMode, enableLightMode } = useAppTheme()

  const handleTinyButtonChange = React.useCallback(
    (value) => {
      if (value) {
        chrome.permissions.request(
          {
            origins: ['<all_urls>'],
          },
          (granted) => {
            if (granted) setEnableTinyButton(value)
          }
        )
      } else {
        setEnableTinyButton(value)
      }
    },
    [setEnableTinyButton]
  )

  const handleTaggingChange = React.useCallback(
    (value) => {
      setTagging(value)
    },
    [setTagging]
  )

  const handlePostPopupChange = React.useCallback(
    (value) => {
      setPostPopup(value)
    },
    [setPostPopup]
  )

  const handleRightClickChange = React.useCallback(
    (value) => {
      setRightClick(value)
    },
    [setRightClick]
  )

  const handleDarkModeChange = (value: boolean) => {
    value ? enableDarkMode() : enableLightMode()
  }

  return (
    <Card header={<FormattedMessage {...messages.header} />}>
      <Card.Section>
        <SwitchOption
          cta={messages.tinyButtonCta}
          description={messages.tinyButtonDescription}
          value={enableTinyButton}
          onChange={handleTinyButtonChange}
          dataHeapId='tiny-button'
          sideElement={
            <BadgeContainer>
              <Badge
                size='regular'
                color={theme.colors.orange.regular}
                textColor={theme.colors.ui.regular}
              >
                <FormattedMessage {...messages.new} />
              </Badge>
            </BadgeContainer>
          }
        />
      </Card.Section>
      <Card.Section>
        <SwitchOption
          cta={messages.taggingCta}
          description={messages.taggingDescription}
          value={tagging}
          onChange={handleTaggingChange}
          dataHeapId='automate-tagging'
        />
      </Card.Section>
      <Card.Section>
        <SwitchOption
          cta={messages.postPopupCta}
          description={messages.postPopupDescription}
          value={postPopup}
          onChange={handlePostPopupChange}
        />
      </Card.Section>
      <Card.Section>
        <SwitchOption
          cta={messages.rightClickCta}
          description={messages.rightClickDescription}
          value={rightClick}
          onChange={handleRightClickChange}
        />
      </Card.Section>
      <Card.Section>
        <SwitchOption
          cta={messages.darkModeCta}
          description={messages.darkModeDescription}
          value={appTheme === 'dark'}
          onChange={handleDarkModeChange}
        />
      </Card.Section>
    </Card>
  )
}

const BadgeContainer = styled.div`
  height: 100%;
  display: flex;
  align-items: center;
  margin-left: 48px;
`

export default Other
