import { css, useTheme } from '@emotion/react'
import type * as React from 'react'

export default function ArrowIcon(
  props: React.SVGProps<SVGSVGElement> & { next?: boolean; disabled?: boolean }
) {
  const theme = useTheme()

  return (
    <svg
      css={css`
        transform: rotate(${getRotation(props.next)}deg);
      `}
      width={16}
      height={16}
      fill='none'
      xmlns='http://www.w3.org/2000/svg'
      {...props}
    >
      <path
        d='M8 0 5.333 2.667l4 3.333H0v4h9.333l-4 3.333L8 16l8-8-8-8Z'
        fill={props.color || theme.colors.pale_peach.regular}
      />
    </svg>
  )
}

function getRotation(next?: boolean) {
  if (!next) return 180
  return 0
}
