import styled from '@emotion/styled'
import { sendMessage } from '@src/lib/messaging'
import { Button, Header } from '@src/ui/components'
import Card from '@src/ui/components/Card'
import { UsersService, useUserStorage } from '@src/v4/services/UsersService'
import * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'
import Browser from 'webextension-polyfill'

const messages = defineMessages({
  header: {
    id: 'settings.myAccount.header',
    defaultMessage: 'My Account',
  },
  subHeader: {
    id: 'settings.myAccount.subHeader',
    defaultMessage: 'Account settings',
  },
  yourCta: {
    id: 'settings.myAccount.your.cta',
    defaultMessage: 'Your Toggl account',
  },
  yourDescription: {
    id: 'settings.myAccount.your.description',
    defaultMessage: `You're currently logged in as {email}`,
  },
  syncCta: {
    id: 'settings.myAccount.sync.cta',
    defaultMessage: 'Sync data',
  },
  syncDescription: {
    id: 'settings.myAccount.sync.description',
    defaultMessage:
      'Use this to manually sync your data (projects, tags etc.) into Toggl Track Browser Extension',
  },
  syncButton: {
    id: 'settings.myAccount.sync.button',
    defaultMessage: 'Sync with Toggl',
  },
  logoutCta: {
    id: 'settings.myAccount.logout.cta',
    defaultMessage: 'Log out',
  },
  logoutDescription: {
    id: 'settings.myAccount.logout.description',
    defaultMessage: 'Log out of Toggl Track Browser Extension',
  },
  logoutButton: {
    id: 'settings.myAccount.logout.button',
    defaultMessage: 'Log out',
  },
})

const MyAccount: React.FC = () => {
  const [currentUser] = useUserStorage()

  const handleSyncClick = React.useCallback(() => {
    sendMessage({ type: 'activate', payload: null })
  }, [])

  const handleLogoutClick = async () => {
    await Browser.runtime.sendMessage({ type: UsersService.logoutKey })

    // Cleanup Tabs
    const activeTabs = await Browser.tabs.query({
      active: true,
      currentWindow: true,
    })

    await Browser.tabs.remove(
      activeTabs.map((tab) => tab.id).filter(Boolean) as number[] // TS is not smart enough...
    )
  }

  return (
    <Root>
      <FormattedMessage tagName={Header} {...messages.header} />
      <Content>
        <Card header={<FormattedMessage {...messages.subHeader} />}>
          <Card.Section>
            <Wrapper>
              <FormattedMessage tagName={Cta} {...messages.yourCta} />
              <FormattedMessage
                tagName={Description}
                {...messages.yourDescription}
                values={{ email: currentUser?.email }}
              />
            </Wrapper>
          </Card.Section>
          <Card.Section>
            <Wrapper>
              <FormattedMessage tagName={Cta} {...messages.syncCta} />
              <FormattedMessage
                tagName={Description}
                {...messages.syncDescription}
              />
            </Wrapper>
            <StyledButton onClick={handleSyncClick}>
              <FormattedMessage {...messages.syncButton} />
            </StyledButton>
          </Card.Section>
          <Card.Section>
            <Wrapper>
              <FormattedMessage tagName={Cta} {...messages.logoutCta} />
              <FormattedMessage
                tagName={Description}
                {...messages.logoutDescription}
              />
            </Wrapper>
            <LogoutButton onClick={handleLogoutClick}>
              <FormattedMessage {...messages.logoutButton} />
            </LogoutButton>
          </Card.Section>
        </Card>
      </Content>
    </Root>
  )
}

export default MyAccount

const Root = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
`

const Content = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 24px;
  gap: 16px;
`

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  gap: 8px;
`

const Cta = styled.span`
  font-size: 14px;
  font-weight: ${({ theme }) => theme.typography.fontWeight.semibold};
  color: ${({ theme }) => theme.colors.ui.regular};
`

const Description = styled.span`
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: ${({ theme }) => theme.typography.fontWeight.normal};
  color: ${({ theme }) => theme.colors.ui.regular};
`

const StyledButton = styled(Button)`
  width: 196px;
  height: 36px;
  margin-bottom: 8px;
  font-weight: ${({ theme }) => theme.typography.fontWeight.semibold};
`

const LogoutButton = styled(StyledButton)`
  color: ${({ theme }) => theme.colors.error_120.regular};
`
