import { css, useTheme } from '@emotion/react'
import styled from '@emotion/styled'
import useSetting from '@src/lib/hooks/useSetting'
import { Badge } from '@src/packages/ui/Badge'
import { Header } from '@src/ui/components'
import type * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'
import Browser from 'webextension-polyfill'

import SwitchOption from '../../components/SwitchOption'

import { Banner } from './Banner'
import { changelog } from './changelog'

const messages = defineMessages({
  header: {
    id: 'settings.whatsNew.header',
    defaultMessage: 'Changelog',
  },
  version: {
    id: 'settings.whatsNew.version',
    defaultMessage: ' Version {version}  {description}',
  },
  latestUpdate: {
    id: 'settings.whatsNew.latestUpdate',
    defaultMessage: 'Latest update',
  },
  newFeatures: {
    id: 'settings.whatsNew.newFeatures',
    defaultMessage: 'New Features:',
  },
  improvements: {
    id: 'settings.whatsNew.improvements',
    defaultMessage: 'Improvements:',
  },
  bugFixes: {
    id: 'settings.whatsNew.bugFixes',
    defaultMessage: 'Bug Fixes:',
  },
  previous: {
    id: 'settings.whatsNew.previous',
    defaultMessage: ' Looking for previous changes? Check it out <a>here</a>',
  },
  footer1: {
    id: 'settings.whatsNew.footer1',
    defaultMessage:
      'For more information on how Toggl Track extension works take a look {br}at the guide:',
  },
  footer2: {
    id: 'settings.whatsNew.footer2',
    defaultMessage:
      'If you have any problems or feedback regarding Toggl Track {br}extension, write to us at:',
  },
  readTheGuide: {
    id: 'settings.whatsNew.readTheGuide',
    defaultMessage: 'Read the guide',
  },
  enableFeature: {
    id: 'settings.whatsNew.enableFeature',
    defaultMessage: 'Enable',
  },
  disableFeature: {
    id: 'settings.whatsNew.disableFeature',
    defaultMessage: 'Disable',
  },
  optOutCta: {
    id: 'settings.whatsNew.optOutCta',
    defaultMessage: 'Whats new page',
  },
  optOutDescription: {
    id: 'settings.whatsNew.optOutDescription',
    defaultMessage:
      'Show this page automatically when the extension is updated',
  },
})

export const WhatsNew: React.FC = () => {
  const theme = useTheme()
  const [showChangelog, setShowChangelog] = useSetting('showChangelog')
  const [tinyButtonEnabled, setTinyButtonEnabled] =
    useSetting('enableTinyButton')
  const [selectedIntegrations, setSelectedIntegrations] =
    useSetting('integrations')

  const latest = changelog[0]

  const onSelectIntegration = (key: string) => {
    const permission = { origins: [`*://*.${key}/`] }
    if (selectedIntegrations[key]) {
      void Browser.permissions.remove(permission).then(() =>
        setSelectedIntegrations({
          ...selectedIntegrations,
          [key]: !selectedIntegrations[key],
        })
      )
    } else {
      void Browser.permissions.request(permission).then((response) => {
        if (!response) return

        setSelectedIntegrations({
          ...selectedIntegrations,
          [key]: !selectedIntegrations[key],
        })
      })
    }
  }

  const handleTogglFeature = (settingName: string, hostName?: string) => {
    if (settingName === 'enableTinyButton') {
      if (tinyButtonEnabled) {
        setTinyButtonEnabled(false)
      } else {
        chrome.permissions.request(
          {
            origins: ['<all_urls>'],
          },
          (granted) => {
            if (granted) {
              setTinyButtonEnabled(true)
            }
          }
        )
      }
    } else if (settingName === 'integration' && hostName) {
      onSelectIntegration(hostName)
    }
  }

  return (
    <Root>
      <Header>
        <FormattedMessage {...messages.header} />
      </Header>
      <Banner />
      <Content>
        <Section>
          <span
            css={css`
              font-weight: 600;
              margin-right: 18px;
            `}
          >
            <FormattedMessage
              {...messages.version}
              values={{
                version: latest.version,
                description: latest.description,
              }}
            />
          </span>
          <Badge
            color={theme.colors.orange.regular}
            textColor={theme.colors.ui.regular}
          >
            <FormattedMessage {...messages.latestUpdate} />
          </Badge>
        </Section>
        <Section>
          <ListContainer>
            {latest.features && (
              <div>
                <strong>
                  <FormattedMessage {...messages.newFeatures} />
                </strong>
                <List>
                  {latest.features.map(
                    ({ title, description, settingsName, hostname }, index) => (
                      <li key={index}>
                        <strong>
                          {title}
                          {': '}
                        </strong>
                        <span>{description}</span>
                        {settingsName && (
                          <span
                            onClick={() =>
                              handleTogglFeature(settingsName, hostname)
                            }
                          >
                            {' ['}
                            {settingsName === 'enableTinyButton' &&
                              (tinyButtonEnabled ? (
                                <FormattedMessage
                                  {...messages.disableFeature}
                                  tagName={EnableFeatureText}
                                />
                              ) : (
                                <FormattedMessage
                                  {...messages.enableFeature}
                                  tagName={EnableFeatureText}
                                />
                              ))}
                            {settingsName === 'integration' &&
                              (selectedIntegrations[hostname ?? ''] ? (
                                <FormattedMessage
                                  {...messages.disableFeature}
                                  tagName={EnableFeatureText}
                                />
                              ) : (
                                <FormattedMessage
                                  {...messages.enableFeature}
                                  tagName={EnableFeatureText}
                                />
                              ))}
                            {']'}
                          </span>
                        )}
                      </li>
                    )
                  )}
                </List>
              </div>
            )}
            {latest.improvements && (
              <div>
                <strong>
                  <FormattedMessage {...messages.improvements} />
                </strong>
                <List>
                  {latest.improvements.map(({ title, description }, index) => (
                    <li key={index}>
                      <strong>
                        {title}
                        {': '}
                      </strong>
                      <span>{description}</span>
                    </li>
                  ))}
                </List>
              </div>
            )}
            {latest.fixes && (
              <div>
                <strong>
                  <FormattedMessage {...messages.bugFixes} />
                </strong>
                <List>
                  {latest.fixes.map(({ title, description }, index) => (
                    <li key={index}>
                      <strong>
                        {title}
                        {': '}
                      </strong>
                      <span>{description}</span>
                    </li>
                  ))}
                </List>
              </div>
            )}
          </ListContainer>
        </Section>
        <Section>
          <SwitchOption
            cta={messages.optOutCta}
            description={messages.optOutDescription}
            value={showChangelog}
            onChange={(value) => setShowChangelog(value)}
            dataHeapId='changelog-opt-out'
          />
        </Section>
        <Footer>
          <div>
            <div>
              <FormattedMessage {...messages.footer1} values={{ br: <br /> }} />
            </div>
            <Link
              href='https://support.toggl.com/en/articles/2206984-toggl-track-browser-extension'
              target='_blank'
              rel='noreferrer noopener'
            >
              <FormattedMessage {...messages.readTheGuide} />
            </Link>
          </div>
          <div>
            <div>
              <FormattedMessage {...messages.footer2} values={{ br: <br /> }} />
            </div>
            <Link href='mailto:support@toggl.com'>{'support@toggl.com'}</Link>
          </div>
        </Footer>
      </Content>
    </Root>
  )
}

const Root = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
`

const Content = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 24px;
  gap: 16px;
`

const Section = styled.div`
  display: flex;
  width: 100%;
  border-radius: 8px;
  background: ${({ theme }) => theme.colors.surface_primary.regular};
  font-size: 14px;
  color: ${({ theme }) => theme.colors.ui.regular};
  padding: 16px;
  align-items: center;
`

const ListContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 24px;
  margin-bottom: 16px;
`

const List = styled.ul`
  padding-inline-start: 24px;
  margin-block-start: 8px;

  & > li {
    padding: 2px 0;
  }
`

const Footer = styled.div`
  background: transparent;
  font-size: 14px;
  color: ${({ theme }) => theme.colors.ui.regular};
  width: 100%;
  max-width: 1000px;
  flex-grow: 0;
  padding: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  & > div {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }
`

const Link = styled.a`
  text-decoration: underline;
  cursor: pointer;
  color: ${({ theme }) => theme.colors.grape_120.regular};
  :hover,
  :focus {
    color: ${({ theme }) => theme.colors.grape_120.hover};
  }
`

const EnableFeatureText = styled.span`
  color: ${({ theme }) => theme.colors.accent_120.regular};
  font-family: Inter;
  font-size: 14px;
  font-weight: 700;
  line-height: 143%;
  margin: 0 4px;
  cursor: pointer;

  &:hover {
    color: ${({ theme }) => theme.colors.accent_120.hover};
  }
`
