import styled from '@emotion/styled'
import useSetting from '@src/lib/hooks/useSetting'
import { OnboardingBanner } from '@src/pages/settings/components/onboardingBanner'
import { Header } from '@src/ui/components'
import type * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'

import CustomScripts from './CustomScripts'
import CustomURLs from './CustomURLs'
import Integrations from './Integrations'

const messages = defineMessages({
  header: {
    id: 'settings.integrations.header',
    defaultMessage: 'Integrations',
  },
})

const GeneralPage: React.FC = () => {
  const [settingsOnboarded, setSettingsOnboardingDone, loaded] =
    useSetting('settingsOnboarded')

  return (
    <Root>
      <Header>
        <FormattedMessage {...messages.header} />
      </Header>
      {loaded && !settingsOnboarded && (
        <OnboardingBanner onClose={() => setSettingsOnboardingDone(true)} />
      )}
      <Content>
        <Integrations />
        <CustomURLs />
        <CustomScripts />
      </Content>
    </Root>
  )
}

export default GeneralPage

const Root = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
`

const Content = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 24px;
  gap: 16px;
`
