import styled from '@emotion/styled'
import useSetting from '@src/lib/hooks/useSetting'
import { Button, TextInput } from '@src/ui/components'
import Card from '@src/ui/components/Card'
import * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'
import Browser from 'webextension-polyfill'

import { CustomScripts } from '@toggl/track-extension'

const messages = defineMessages({
  header: {
    id: 'settings.integrations.customScripts.header',
    defaultMessage: 'Custom Scripts for Integrations',
  },
  cta: {
    id: 'settings.integrations.customScripts.cta',
    defaultMessage:
      'Support of custom workflows for users on enterprise and premium plan.',
  },
  button: {
    id: 'settings.integrations.customScripts.button',
    defaultMessage: 'Add',
  },
  delete: {
    id: 'settings.integrations.customScripts.delete',
    defaultMessage: 'Delete',
  },
})

const CustomScriptInput: React.FC = () => {
  const inputsContainer = React.useRef<HTMLDivElement>(null)
  const [customScripts, setCustomScripts] = useSetting('customScripts')
  const [domain, setDomain] = React.useState('')
  const [scriptCode, setScriptCode] = React.useState('')

  const handleDomainChange = React.useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => setDomain(e.target.value),
    [setDomain]
  )

  const handleScriptCodeChange = React.useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => setScriptCode(e.target.value),
    [setScriptCode]
  )

  const handleAddClick = React.useCallback(() => {
    if (domain.trim() === '') return
    if (!CustomScripts[scriptCode]) {
      return
    }

    const permission = { origins: [`*://*.${domain}/`] }

    void Browser.permissions.request(permission).then((response) => {
      if (!response) return

      setCustomScripts({
        ...customScripts,
        [domain]: CustomScripts[scriptCode],
      })

      setDomain('')
      setScriptCode('')
    })
  }, [domain, setCustomScripts, customScripts, scriptCode])

  const handleDeleteClick = React.useCallback(
    (key: string) => {
      const { [key]: _, ...rest } = customScripts
      setCustomScripts(rest)
    },
    [customScripts, setCustomScripts]
  )

  return (
    <Card header={<FormattedMessage {...messages.header} />}>
      <Card.Section>
        <FormattedMessage tagName='span' {...messages.cta} />
        <List>
          {Object.entries(customScripts).map(([integration, { name }]) => (
            <ListItem key={integration}>
              <span>
                <CaptionItem>{integration}</CaptionItem>
                {` - ${name}`}
              </span>
              <DeleteButton onClick={() => handleDeleteClick(integration)}>
                <FormattedMessage {...messages.delete} />
              </DeleteButton>
            </ListItem>
          ))}
        </List>
        <Container ref={inputsContainer}>
          <StyledTextInput
            placeholder='Domain'
            value={domain}
            onChange={handleDomainChange}
          />

          <StyledTextInput
            placeholder='Script Code'
            value={scriptCode}
            onChange={handleScriptCodeChange}
          />
        </Container>
        <StyledButton id='custom-scripts' onClick={handleAddClick}>
          <FormattedMessage {...messages.button} />
        </StyledButton>
      </Card.Section>
    </Card>
  )
}

const List = styled.div`
  width: 400px;
  height: 100px;
  padding: 8px;
  gap: 4px;
  overflow: auto;

  background: ${({ theme }) => theme.colors.surface_primary.regular};
  border: 1px solid ${({ theme }) => theme.colors.ui_040.regular};
  border-radius: 8px;
`

const ListItem = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  line-height: 20px;
  padding: 0 20px 0 12px;
  font-size: 14px;
`

const CaptionItem = styled.span`
  font-weight: ${({ theme }) => theme.typography.fontWeight.bold};
`

const DeleteButton = styled.span`
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  font-size: 14px;
  font-weight: ${({ theme }) => theme.typography.fontWeight.semibold};
  color: ${({ theme }) => theme.colors.error_120.regular};
  cursor: pointer;
`

const Container = styled.div`
  display: flex;
  flex-direction: row;
  gap: 16px;
`

const StyledTextInput = styled(TextInput)`
  width: 192px;
  height: 36px;

  border-radius: 8px;
`

const StyledButton = styled(Button)`
  width: 74px;
`

export default CustomScriptInput
