import styled from '@emotion/styled'
import Card from '@src/ui/components/Card'
import type * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'
import { Link } from 'react-router-dom'

const messages = defineMessages({
  header: {
    id: 'settings.general.welcome.header',
    defaultMessage: 'Start tracking time in your favorite apps',
  },
  welcome: {
    id: 'settings.general.welcome.message',
    defaultMessage:
      'You can add Toggl Track Browser Extension to over 130 tools, including Trello, Asana, JIRA and many more.',
  },
  cta: {
    id: 'settings.general.welcome.cta',
    defaultMessage:
      'Visit the <link>Integrations page</link> to use this feature.',
  },
})

const Welcome: React.FC = () => {
  return (
    <Card header={<FormattedMessage {...messages.header} />}>
      <Content>
        <FormattedMessage tagName='span' {...messages.welcome} />

        <FormattedMessage
          tagName='span'
          {...messages.cta}
          values={{
            link: (chunk) => (
              <StyledLink to='/integrations'>{chunk}</StyledLink>
            ),
          }}
        />
      </Content>
    </Card>
  )
}

const Content = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 16px;
  padding-top: 16px;
`

const StyledLink = styled(Link)`
  text-decoration: underline;
  cursor: pointer;
  color: ${({ theme }) => theme.colors.grape_120.regular};
  :hover,
  :focus {
    color: ${({ theme }) => theme.colors.grape_120.hover};
  }
`

export default Welcome
