import styled from '@emotion/styled'
import useSetting from '@src/lib/hooks/useSetting'
import { Header } from '@src/ui/components'
import Card from '@src/ui/components/Card'
import * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'

import SwitchOption from '../components/SwitchOption'

const messages = defineMessages({
  header: {
    id: 'settings.telemetry.header',
    defaultMessage: 'Telemetry',
  },
  subHeader: {
    id: 'settings.telemetry.subHeader',
    defaultMessage: 'Telemetry',
  },
  sendUsageStatisticsCta: {
    id: 'settings.telemetry.sendUsageStatistics.cta',
    defaultMessage: 'Send usage statistics',
  },
  sendUsageStatisticsDescription: {
    id: 'settings.telemetry.sendUsageStatistics.description',
    defaultMessage:
      'Help us understand how Toggl Track Browser Extension is used by sending usage statistics',
  },
  sendErrorReportsCta: {
    id: 'settings.telemetry.sendErrorReports.cta',
    defaultMessage: 'Send error reports',
  },
  sendErrorReportsDescription: {
    id: 'settings.telemetry.sendErrorReports.description',
    defaultMessage:
      'Submit error reports to help us fix issues with Toggl Track Browser Extension',
  },
})

const Telemetry: React.FC = () => {
  const [sendUsageStatistics, setSendUsageStatistics] = useSetting(
    'sendUsageStatistics'
  )
  const [sendErrorReports, setSendErrorReports] = useSetting('sendErrorReports')

  const handleSendUsageStatisticsChange = React.useCallback(
    (value) => {
      setSendUsageStatistics(value)
    },
    [setSendUsageStatistics]
  )

  const handleSendErrorReportsChange = React.useCallback(
    (value) => {
      setSendErrorReports(value)
    },
    [setSendErrorReports]
  )

  return (
    <Root>
      <Header>
        <FormattedMessage {...messages.header} />
      </Header>
      <Content>
        <Card header={<FormattedMessage {...messages.subHeader} />}>
          <Card.Section>
            <SwitchOption
              cta={messages.sendUsageStatisticsCta}
              description={messages.sendUsageStatisticsDescription}
              value={sendUsageStatistics}
              onChange={handleSendUsageStatisticsChange}
            />
          </Card.Section>
          <Card.Section>
            <SwitchOption
              cta={messages.sendErrorReportsCta}
              description={messages.sendErrorReportsDescription}
              value={sendErrorReports}
              onChange={handleSendErrorReportsChange}
            />
          </Card.Section>
        </Card>
      </Content>
    </Root>
  )
}

export default Telemetry

const Root = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
`

const Content = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 24px;
  gap: 16px;
`
