import { css } from '@emotion/react'
import styled from '@emotion/styled'
import Dropdown from '@src/components/Dropdown'
import { type DEFAULT_SETTINGS } from '@src/lib/constants'
import useSetting from '@src/lib/hooks/useSetting'
import Card from '@src/ui/components/Card'
import * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'

const messages = defineMessages({
  title: {
    id: 'settings.general.projects.rememberLast.title',
    defaultMessage: 'Remember last chosen project',
  },
  copy: {
    id: 'settings.general.projects.rememberLast.copy',
    defaultMessage:
      'Remembers the last project you used. This overrides the "Default project" setting.',
  },
  dontRememberProject: {
    id: 'settings.general.dontRememberProject',
    defaultMessage: "Don't remember last project",
  },
  rememberProjectPerIntegration: {
    id: 'settings.general.rememberProjectPerIntegration',
    defaultMessage: 'Remember for each integration',
  },
  rememberProjectPerUrl: {
    id: 'settings.general.rememberProjectPerUrl',
    defaultMessage: 'Remember for each URL / webpage',
  },
})

const idToMessage = {
  none: 'dontRememberProject',
  integration: 'rememberProjectPerIntegration',
  url: 'rememberProjectPerUrl',
}

export default function RememberLast() {
  const [rememberProjectPer, setRememberProjectPer] =
    useSetting('rememberProjectPer')

  React.useEffect(() => {
    if (!idToMessage[rememberProjectPer]) setRememberProjectPer('none')
  }, [rememberProjectPer, setRememberProjectPer])

  const handleSelected = React.useCallback(
    (id: string) => {
      setRememberProjectPer(id as typeof DEFAULT_SETTINGS['rememberProjectPer'])
    },
    [setRememberProjectPer]
  )

  return (
    <Card.Section>
      <FormattedMessage tagName={Cta} {...messages.title} />
      <FormattedMessage tagName='span' {...messages.copy} />
      <Dropdown
        selected={rememberProjectPer}
        onItemClick={handleSelected}
        css={css`
          width: 360px;
        `}
      >
        <Dropdown.Trigger>
          {!!idToMessage[rememberProjectPer] && (
            <FormattedMessage {...messages[idToMessage[rememberProjectPer]]} />
          )}
        </Dropdown.Trigger>
        <Dropdown.Overlay />
        <Dropdown.Content>
          <Dropdown.Item id='none'>
            <FormattedMessage {...messages.dontRememberProject} />
          </Dropdown.Item>
          <Dropdown.Item id='integration'>
            <FormattedMessage {...messages.rememberProjectPerIntegration} />
          </Dropdown.Item>
          <Dropdown.Item id='url'>
            <FormattedMessage {...messages.rememberProjectPerUrl} />
          </Dropdown.Item>
        </Dropdown.Content>
      </Dropdown>
    </Card.Section>
  )
}

const Cta = styled.h5`
  font-size: 14px;
  margin-bottom: -8px;
  font-weight: ${({ theme }) => theme.typography.fontWeight.semibold};
`
